.container {
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.scene {
  perspective: 400px;
}

.box {
  position: relative;
  width: 200px;
  height: 200px;

  transform-style: preserve-3d;
  transform: translateZ(-100px);
  transition: 0.3s;
  animation-name: rotateAnimation;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.facet {
  position: absolute;
  width: 200px;
  height: 200px;

  font-size: 120px;
  line-height: 200px;
  text-align: center;
  color: white;
  text-shadow: var(--facet);
  border: 1px solid var(--accent-text-color);

  &.front {
    transform: rotateY(0deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
  }

  &.back {
    transform: rotateY(90deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
  }

  &.right {
    transform: rotateY(180deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
  }

  &.left {
    transform: rotateY(-90deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
  }

  &.top {
    transform: rotateX(90deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
    font-size: 65px;
  }

  &.bottom {
    transform: rotateX(-90deg) translateZ(100px);
    background: var(--shadowNotFoundPage3);
  }
}

@keyframes rotateAnimation {
  25% {
    transform: translateZ(-100px) rotateY(-90deg);
  }
  50% {
    transform: translateZ(-100px) rotateY(-180deg);
  }
  75% {
    transform: translateZ(-100px) rotateX(-90deg);
  }
  85% {
    transform: translateZ(-100px) rotateX(-90deg);
  }
}

.shadow {
  position: absolute;
  z-index: -1;
  left: -50px;
  top: calc(100% + 5px);

  width: calc(100% + 100px);
  height: 57px;

  opacity: 35%;
  border-radius: 50%;
  background: linear-gradient(to bottom, var(--shadowNotFoundPage1), var(--shadowNotFoundPage2));
  filter: blur(20px);
}

.desk {
  display: flex;
  margin-top: 60px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.oops {
  font-size: 75px;
  font-weight: 700;
  text-shadow: var(--text-box-shadow);
  margin-bottom: 40px;
}

.oops span {
  display: inline-block;
}

.oops span:nth-of-type(2) {
  animation-delay: 0.05s;
}
.oops span:nth-of-type(3) {
  animation-delay: 0.1s;
}
.oops span:nth-of-type(4) {
  animation-delay: 0.15s;
}
.oops span:nth-of-type(5) {
  animation-delay: 0.2s;
}
.oops span:nth-of-type(6) {
  animation-delay: 0.25s;
}
.oops span:nth-of-type(7) {
  animation-delay: 0.3s;
}
.oops span:nth-of-type(8) {
  animation-delay: 0.35s;
}
.oops span:nth-of-type(9) {
  animation-delay: 0.4s;
}
.oops span:nth-of-type(10) {
  animation-delay: 0.45s;
}
.oops span:nth-of-type(11) {
  animation-delay: 0.5s;
}
.oops span:nth-of-type(12) {
  animation-delay: 0.55s;
}
.oops span:nth-of-type(13) {
  animation-delay: 0.6s;
}
.oops span:nth-of-type(14) {
  animation-delay: 0.65s;
}
.oops span:nth-of-type(15) {
  animation-delay: 0.7s;
}
.oops span:nth-of-type(16) {
  animation-delay: 0.75s;
}
.oops span:nth-of-type(17) {
  animation-delay: 0.8s;
}
.oops span:nth-of-type(18) {
  animation-delay: 0.85s;
}
.oops span:nth-of-type(19) {
  animation-delay: 0.9s;
}
.oops span:nth-of-type(20) {
  animation-delay: 0.95s;
}

.animate span {
  opacity: 0;
  transform: translate(-300px, 0) scale(0);
  animation: sideSlide 0.5s forwards;
  color: var(--shadowNotFoundPage3);
  text-shadow: var(--facet);
}

@keyframes sideSlide {
  60% {
    transform: translate(20px, 0) scale(1);
  }

  80% {
    transform: translate(20px, 0) scale(1);
  }

  99% {
    transform: translate(0) scale(1.2);
    color: var(--base1);
  }

  100% {
    transform: translate(0) scale(1);
    opacity: 1;
  }
}
